/*Montserrat 400*/
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-display: swap;
}
/*Montserrat 500*/
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-display: swap;
}
/*Montserrat 600*/
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-semi-bold.woff2") format("woff2"), url("../fonts/montserrat-semi-bold.woff") format("woff");
  font-display: swap;
}
/*Montserrat 700*/
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  background-color: #f9fbfd;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #011c40;
}
.wrapper .main-header,
.wrapper .main-footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.main-header {
  position: absolute;
  width: 100%;
  padding: 35px 0;
  z-index: 2;
}

.main-header__container {
  width: 100%;
  max-width: 1260px;
}

@media (max-width: 1023px) {
  .main-header {
    padding: 27px 0;
  }
}
@media (max-width: 767px) {
  .main-header {
    padding: 20px 0;
  }
  .main-header--nojs {
    position: relative;
  }
  .main-header__container {
    min-height: 22px;
  }
  .main-header__overlay {
    position: fixed;
    display: none;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 43, 102, 0.5);
    z-index: 10;
    cursor: pointer;
  }
}
.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navigation__logo {
  margin-right: 50px;
}

.navigation__logo-link {
  display: block;
  width: 70px;
  height: 38px;
}
.navigation__logo-link:hover, .navigation__logo-link:focus {
  filter: drop-shadow(0 0 7px #ffffff);
  transition: 0.3s;
}
.navigation__logo-link:active {
  opacity: 0.7;
  filter: drop-shadow(0 0 7px #ffffff);
  transition: 0.3s;
}

.navigation__logo-img {
  width: 70px;
  height: 38px;
  fill: #f9fbfd;
}

.navigation__toggle {
  display: none;
}

.navigation__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 34px;
  max-width: 950px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.navigation__link {
  font-weight: 500;
  line-height: 20px;
  color: #f9fbfd;
}
.navigation__link:hover, .navigation__link:focus {
  filter: drop-shadow(0 0 7px #ffffff);
  transition: 0.3s;
}
.navigation__link:active {
  opacity: 0.7;
  filter: drop-shadow(0 0 7px #ffffff);
  transition: 0.3s;
}

@media (max-width: 1023px) {
  .navigation__logo-link {
    width: 38px;
    height: 20px;
  }
  .navigation__logo-img {
    width: 38px;
    height: 20px;
  }
  .navigation__list {
    gap: 24px;
    max-width: 550px;
  }
  .navigation__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .navigation {
    position: relative;
  }
  .navigation__logo-link {
    width: 42px;
    height: 22px;
  }
  .navigation__logo-img {
    width: 42px;
    height: 22px;
  }
  .navigation__toggle {
    position: absolute;
    top: -10px;
    right: -8px;
    display: block;
    width: 40px;
    height: 40px;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    cursor: pointer;
    z-index: 11;
    user-select: none;
  }
  .navigation--closed .navigation__toggle::before {
    content: "";
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translateX(-50%);
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd;
  }
  .navigation--opened .navigation__logo {
    position: absolute;
    top: -1px;
    left: 12%;
    margin: 0;
    z-index: 11;
  }
  .navigation--opened .navigation__logo-img {
    fill: #011c40;
  }
  .navigation--opened .navigation__toggle {
    right: -2px;
  }
  .navigation--opened .navigation__toggle::before {
    content: "";
    position: absolute;
    top: 20px;
    right: 0;
    width: 18px;
    height: 2px;
    background-color: #011c40;
    transform: rotate(45deg);
    box-shadow: none;
  }
  .navigation--opened .navigation__toggle::after {
    content: "";
    position: absolute;
    top: 20px;
    right: 0;
    width: 18px;
    height: 2px;
    background-color: #011c40;
    transform: rotate(-45deg);
  }
  .navigation__list {
    flex-direction: column;
    justify-content: flex-start;
  }
  .navigation--closed .navigation__item {
    display: none;
  }
  .navigation--opened .navigation__list {
    position: absolute;
    top: -20px;
    right: -15px;
    width: 98%;
    max-width: 700px;
    height: 101vh;
    align-items: flex-start;
    background-color: #f9fbfd;
    padding-top: 74px;
    padding-left: 15px;
    row-gap: 10px;
    z-index: 10;
    overflow-y: scroll;
  }
  .navigation__link {
    display: block;
    padding: 10px 10px 8px 10px;
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
  .navigation__link:hover, .navigation__link:focus {
    filter: drop-shadow(0 0 7px #011c40);
    transition: 0.3s;
  }
  .navigation__link:active {
    opacity: 0.7;
    filter: drop-shadow(0 0 7px #011c40);
    transition: 0.3s;
  }
  .navigation--nojs .navigation__toggle {
    display: none;
  }
  .navigation--nojs .navigation__list {
    flex-direction: column;
    margin: 0 auto;
    width: 100%;
    row-gap: 0;
  }
  .navigation--nojs .navigation__item {
    display: block;
    margin-bottom: 0;
  }
  .navigation--nojs .navigation__link {
    color: #f9fbfd;
  }
}
.promo {
  position: relative;
  min-height: 650px;
  padding: 270px 0 278px;
  background-color: #011c40;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
}

.promo__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 0 90%;
}

.promo__container {
  position: relative;
  padding-left: 145px;
  z-index: 1;
}

.promo__container h1 {
  margin: 0;
  margin-bottom: 12px;
  padding: 0;
  font-size: 54px;
  font-weight: 700;
  line-height: 66px;
  color: #f9fbfd;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.promo__container p {
  margin: 0;
  padding: 0;
  font-size: 20px;
  line-height: 24px;
  color: #f9fbfd;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

@media (max-width: 1023px) {
  .promo {
    min-height: 638px;
    padding: 268px 0 275px;
    filter: none;
  }
  .promo__container h1 {
    font-size: 50px;
    line-height: 61px;
    text-shadow: none;
  }
  .promo__container p {
    font-size: 18px;
    line-height: 22px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .promo {
    min-height: 500px;
    padding: 176px 0 178px;
  }
  .promo__container h1 {
    margin-bottom: 10px;
    font-size: 36px;
