    font-size: 16px;
  }
}
.about {
  width: 100%;
  padding: 120px 0 145px;
}

.about__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  width: 100%;
  max-width: 1260px;
}

.about__content {
  max-width: 512px;
  padding-top: 56px;
}

.about__content h2 {
  margin: 0 0 23px;
  padding: 0;
  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
}

.about__content p {
  margin: 0;
  padding: 0;
}

.about__img {
  width: 100%;
  height: auto;
}

@media (max-width: 1023px) {
  .about {
    padding: 100px 0 93px;
  }
  .about__container {
    align-items: flex-start;
    gap: 30px;
  }
  .about__content {
    padding-top: 0;
  }
  .about__content h2 {
    margin: -9px 0 15px;
    font-size: 26px;
    line-height: 36px;
  }
}
@media (max-width: 767px) {
  .about {
    padding: 65px 0 85.13px;
  }
  .about__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 44px;
  }
  .about__content {
    max-width: 737px;
  }
  .about__content h2 {
    font-size: 24px;
    line-height: 34px;
  }
}
.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
