.social__list {
  display: flex;
  flex-wrap: wrap;
  gap: 27px;
  max-width: 200px;
  margin: 0;
  margin-top: 10px;
  padding: 0;
  list-style-type: none;
}

.social__link {
  display: block;
  width: 20px;
  height: 20px;
}
.social__link:hover, .social__link:focus {
  filter: drop-shadow(0 0 7px #ffffff);
  transition: 0.3s;
}
.social__link:active {
  opacity: 0.7;
  filter: drop-shadow(0 0 7px #ffffff);
  transition: 0.3s;
}

.social__icon {
  width: 20px;
  height: 20px;
  fill: #f9fbfd;
}

@media (max-width: 1023px) {
  .social__list {
    gap: 21.4px;
    max-width: 120px;
    margin-top: 3px;
  }
  .social__link {
    width: 16px;
    height: 16px;
  }
  .social__icon {
    width: 16px;
    height: 16px;
  }
}
@media (max-width: 767px) {
  .social {
    margin-left: -4px;
  }
  .social__list {
    gap: 21px;
    max-width: 700px;
    margin-top: 0;
  }
}
